.header {
  $this: &;
  color: $black-light;
  &__top {
    background-color: $white-dark;
    #{$this}__inner {
      justify-content: space-between;
      // padding-top: 10px
 } }      // padding-bottom: 10px
  &__bottom {
    background-color: $accent; }
  &__inner {
    @include flex(flex-start, center);
    padding-top: 10px;
    padding-bottom: 10px;
    @include xs {
      justify-content: space-between;
      padding-top: 5px;
      padding-bottom: 5px; } }
  &__adress {
    @include os-reg; }
  &__social {
    font-size: $fz-sm;
    color: $gray;
    @include flex(flex-start, center);
    @include sm {
      color: $black-light;
      justify-content: center;
      margin-bottom: 15px;
      span:first-child {
        display: none; } } }
  &__social-block {
    @include flex(space-around, center);
    flex-grow: 1;
    @include sm {
      display: none; } }
  &__social-block-mob {
    display: none;
    @include sm {
      display: block; } }
  &__mail {
    @include os-reg;
    .icon {
      margin-right: 5px;
      font-size: 24px;
      position: relative;
      top: 0.2em; }
    @include lg {
      flex-grow: 1;
      text-align: right; }
    @include xs {
      display: none; } }
  &__mail-mob {
    display: none;
    @include xs {
      display: block; } }
  &__call {
    margin-left: 50px;
    @include xs {
      margin-left: 0; } }
  &__nav {
    flex-grow: 1;
    text-align: right;
    @include lg {
      position: fixed;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      z-index: 10;
      background-color: $accent;
      text-align: left;
      padding: 15px;
      overflow: auto;
      @include vertical;
      transform: translate(0, -100%);
      @include tr(transform .3s);
      &.is-active {
        transform: translate(0, 0); } } }
  &__nav-in {
    @include lg {
      display: inline-block;
      vertical-align: middle;
      font-size: $fz;
      @include sm {
        font-size: $fz-sm; } } }
  &__burger {
    display: none;
    margin-left: 15px;
    @include lg {
      display: block; } }
  &__close {
    @include icon-close(30, 1, currentColor);
    display: none;
    position: absolute;
    top: 15px;
    right: 15px;
    z-index: 1;
    @include lg {
      display: block; } } }

.header-call {
  @include os-reg;
  @include flex(flex-start, flex-end, column);
  .icon {
    font-size: 24px;
    margin-right: 5px;
    position: relative;
    top: 0.2em;
    @include xs {
      font-size: 18px; } }
  span {
    color: #9d7a00;
    text-decoration: underline;
    font-size: $fz-sm;
    @include light;
    @include xs {
      display: none; } } }


.lang {
  text-transform: uppercase;
  font-size: $fz-sm;
  li {
    display: inline-block;
    @include notfirst {
      margin-left: 10px; } }
  a {
    @include tr(color .3s);
    @include hover {
      color: $accent;
      text-decoration: underline; }
    &.is-active {
      color: $accent;
      text-decoration: underline; } } }

.burger {
  display: block;
  width: 28px;
  height: 28px;
  margin-top: -1.3em;
  // color: $white
  span:after, span:before {
    content: "";
    position: absolute;
    left: 0;
    top: 9px; }
  span:after {
    top: 18px; }
  span {
    position: relative;
    display: block; }
  span, span:after, span:before {
    width: 100%;
    height: 2px;
    background-color: currentColor;
    transition: all 0.3s;
    backface-visibility: hidden;
    border-radius: 2px; }
  // &.is-active span
  //   background-color: transparent
  // &.is-active span:before
  //   transform: rotate(45deg) translate(-1px, 0px)
  // &.is-active span:after
 }  //   transform: rotate(-45deg) translate(6px, -7px)
