.contacts {
  @include flex(flex-start, normal, row, wrap);
  &__content {
    width: percentage(240/690);
    .contact {
      @include notlast {
        margin-bottom: 25px;
        @include md {
          margin-bottom: 15px; } } } }
  &__social {
    text-align: center; }
  &__btn {
    text-align: right;
    margin-top: 25px;
    width: 100%;
    padding-right: 40px; }
  &__btn-in {
    max-width: 330px;
    margin-left: auto; }
  &__map {
    position: relative;
    padding-top: 40%;
    width: percentage(450/690);
    iframe {
      @include coverdiv; } } }

.contact {
  @include os-reg;
  color: $black-light;
  position: relative;
  padding-left: 30px;
  &__icon {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate(0, -50%);
    .icon {
      font-size: 25px; } }
  &__content {
    a {
      text-decoration: underline; } } }

