.nav {
  @include os-bold;
  li {
    display: inline-block;
    margin-right: 60px;
    @include xl {
      margin-right: 30px; }
    @include lg {
      margin-right: 0;
      display: block;
      margin-bottom: 15px; } }
  a {
    @include tr(color .3s);
    &.is-active {
      color: $white; }
    @include hover {
      color: $white; }
    em {
      @include os-reg; } }

  &--footer {
    @include light;
    li {
      display: block;
      margin-right: 0;
      @include notlast {
        margin-bottom: 10px;
        @include md {
          margin-bottom: 5px; } } }
    a {
      @include hover {
        color: $black-light; }
      em {
        color: $black-light; } } } }

.nav-hero {
  ul {
    @include flex(flex-start, normal, row, wrap); }
  li {
    margin-bottom: 15px;
    width: 50%;
    @include md {
      margin-bottom: 10px; }
    @include sm {
      margin-bottom: 5px; }
    &::before {
      content: '-';
      display: inline-block;
      // vertical-align: middle
      margin-right: 5px; } }
  a {
    text-decoration: underline;
    @include tr(color .3s);
    @include hover {
      text-decoration: none;
      color: $accent; }
    &.is-active {
      text-decoration: none;
      color: $accent; } } }



